import React, { FC, useRef, useState, useEffect, useMemo } from 'react';
import QueryTable, { Props as QueryTableProps } from '@/components/QueryTable';
import { ColumnsType } from 'antd/lib/table';
import { withAuthorized } from '@/utils/Authorized';
import { Role } from '@/utils/authority';
import { useGetUserCommontNum } from '@/services/publicServices';
import { Avatar, message, Space } from 'antd';
import { UserOutlined } from '@ant-design/icons';
import { Link } from 'umi';
import { useGetUserList } from './services';
import PreviewImg from '@/components/PreviewImg';
import { UserOutline, UserBlack } from '@/components/miniComponents';
import { UserStatusMaps, UserLevelMaps, UserLevel } from '@/utils/constant.js';
import moment from 'moment';
import { UserDetailInfo } from '@/types/types';
import CopyId from '@/components/CopyId';

interface QueryParams {
  current: number;
  pageSize: number;
  [key: string]: any;
}

const ROLE_TYPE = 'common';
const PAGESIZE = 10;
const initSearch = {
  pagination: { pageSize: PAGESIZE, current: 1 },
  query: { roleType: ROLE_TYPE },
  sort: { type: 'desc', key: 'create_time' },
};

const NormalUserList: FC = () => {
  const formRef = useRef<any>();
  const queryTable = useRef<any>();
  const [searchParams, setSearchParams] = useState(initSearch);
  const { userList, pagination, isValidating, mutate } = useGetUserList(
    searchParams
  );
  const userIds = useMemo(() => {
    return userList?.map((item: UserDetailInfo) => item?.userId);
  }, [userList]);
  const {
    UserCommontNum,
    isValidating: commontNumLoading,
  } = useGetUserCommontNum(userIds);

  // 查询列表数据
  function query(params: QueryParams) {
    // eslint-disable-next-line no-shadow
    const { pagination, query } = params;

    setSearchParams((preParams: any) => ({
      ...preParams,
      pagination,
      query: {
        ...query,
        roleType: ROLE_TYPE,
      },
    }));
  }
  // Todo 评论条数和最后评论时间字段暂时没有
  const columns: ColumnsType<any> = [
    {
      title: '用户头像',
      dataIndex: 'userAvatar',
      width: 120,
      render: (text: string) => (
        <PreviewImg imgUrls={text} placement="rightBottom">
          <Avatar size={64} src={text} icon={<UserOutlined />} />
        </PreviewImg>
      ),
    },
    {
      title: '用户名',
      dataIndex: 'nickName',
      render: (text: string, record: any) => (
        <>
          <Link to={`/user-manage/user/${record?.userId}`}>{text}</Link>
          <CopyId
            text={record?.userId}
            iconStyle={{ marginLeft: 5, color: '#1890ff' }}
          />
        </>
      ),
    },
    // {
    //   title: '封禁状态',
    //   dataIndex: 'status',
    //   render: (text: number) => (
    //     text === 110
    //       ? <span style={{ color: '#ff4d4f' }}>封禁</span>
    //       : '正常'
    //   )
    // },
    {
      title: '评论条数',
      render: (_: any, record: any) => UserCommontNum[record?.userId] || 0,
    },
    // todo 修改字段名
    {
      title: '优质评论条数',
      dataIndex: 'highQualityCount',
      render: (text: any, record: any) => text || 0,
    },
    {
      title: '最后评论时间',
      dataIndex: 'updateTime',
      render: (text: number) => moment(text).format('YYYY/MM/DD HH:mm'),
    },
    // todo 修改字段名
    {
      title: '积分',
      dataIndex: 'rewardPoint',
      render: (text: any, record: any) => text || 0,
    },
    {
      title: '等级',
      render: (_: any, record: any) => UserLevel[record?.userLevel] || 'lv.1',
    },
    {
      title: '操作',
      render: (_: any, record: any) => (
        <Space size={8}>
          {/* <UserOutline user={ record } refresh={ mutate } /> */}
          <UserBlack user={record} refresh={mutate} isEnter={true} />
        </Space>
      ),
    },
  ];

  const formItem: QueryTableProps['formItem'] = [
    {
      name: 'nickName',
      label: '用户名',
      span: 8,
      renderCom: 'input',
    },
    {
      name: 'userLevel',
      span: 8,
      label: '等级',
      renderCom: 'select',
      comProps: {
        options: UserLevelMaps,
      },
    },
    // {
    //   name: 'status',
    //   span: 8,
    //   label: '状态',
    //   renderCom: 'select',
    //   comProps: {
    //     options: UserStatusMaps
    //   }
    // },
  ];

  return (
    <React.Fragment>
      <QueryTable
        notQueryWhilestart={true}
        formItem={formItem}
        columns={columns}
        rowKey="id"
        formRef={formRef}
        ref={queryTable}
        query={query}
        list={userList}
        total={pagination?.total || 0}
        loading={isValidating}
      />
    </React.Fragment>
  );
};

export default withAuthorized(NormalUserList, [Role.SYSTEM, Role.COMMENT]);
// export default NormalUserList;
